.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #bed116;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #1141e0;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.card{
  display: flex;
  gap:2rem;
  align-items: center;
  padding-top:150px;
}
.card-ctn{
  background-color: #e4ce0a;
  width: 40vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 0 50px black;
  cursor: default;
}
button{
  width:10em;
  height: 4em;
  border: none;
  border-radius: 5px;
  transition:all 0.7s ease;
  cursor: pointer;
}
.header{
  color:rgb(127, 19, 177);

}
button:hover{
  transform: scale(1.1);
  background-color:rgb(247, 38, 73);
  color: yellow;
  font-weight: bolder;
}
.body {
text-align: center;
}